.App {
  font-family: sans-serif;
  text-align: center;
}
.listNumber{
  list-style-type: decimal
}

.project-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  @media (max-width: 960px) {
    display: block;
  }
}
.part-title {
  padding-bottom: 0.5rem;
}
.menu {
  color: #ffffff;
  display: none;
  @media (min-width: 960px) {
    display: block;
  }
}

.side-menu{
  list-style: none;
  padding: 0 0 0 2rem;
  position: sticky;
  top: 3rem;
  & > li{
    margin-bottom: 1.5rem;
  }
  li > ul {
    list-style: none;
    padding-left: 0.875rem;
    li {
      margin: 0 0 0.3rem 0;
    }
    button {
      // font-size: 0.875rem;
    }
  }
}
.side-menu-title {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

